___CSS_LOADER_EXPORT___.push([module.id, `body {
  font-family: Consolas;
  background-color: rgb(249, 249, 249);
  margin: 0;
  display: flex;
  height: 100vh;
  width: 100vw;
}

#sidebar-title-container { 
  display: flex;
  align-items: center;
  gap: 10px;
  margin-bottom: 10px;
}

#sidebar-title { 
  font-size: larger;
  font-weight: bold;
}

#todo-logo { 
  height: 30px;
}

#sidebar { 
  background-color: rgb(241, 245, 249);
  display: flex;
  flex-direction: column;
  flex: 1;
  padding: 10px;
}

#sidebar-projects {
  display: flex;
  flex-direction: column;
  gap: 5px;
}

.project-link { 
  height: 20px;
}

.project-link:hover { 
  background-color: rgba(190, 190, 190, 0.116);
  /* border-radius: 2px; */
}
  
#add-project-button { 
  height: 20px;
  margin-top: 5px;
  border: 1.5px dashed black;
  background-color: rgba(190, 190, 190, 0);
  font-weight: bolder;
  display: flex;
  justify-content: center;
  align-items: center;
}

#add-project-button:hover { 
  background-color: rgba(190, 190, 190, 0.116);
}

#add-icon { 
  height: 15px;
}

#main-container { 
  flex: 7;
  padding: 50px;
}

#project-title { 
  font-size: 40px;
  font-weight: bold;
  margin-bottom: 10px;
}

#todo-container { 
  display: flex;
  gap: 0px;
}


.todo-item:hover { 
  background-color: rgba(190, 190, 190, 0.116);
}

.todo-header { 
  display: flex;
  justify-content: flex-start;
  padding: 0;
  margin: 0;
}
.editable-textbox, .add-todo-input { 
  font: inherit;
  font-size: small;
  background-color: rgba(255, 255, 255, 0);
  border: none;
  padding: 0px;
  margin: 0px;
  outline: none;
}


.editable-textbox:focus-visible  { 
  outline: none;
  border-bottom: 1px solid black;
}

#project-title.editable-textbox:focus-visible  { 
  outline: none;
  border-bottom: 3px solid black;
  /* background-color: blue; */
}

.add-todo-container { 
  display: flex;
  align-items: center;
}

.add-todo-container:hover { 
  background-color: rgba(190, 190, 190, 0.116);
}

.todo-details { 
  display: flex;
  justify-content: flex-start;
  align-items: center;
  margin-left: 20px;
  font-size: small;
  padding: 5px;
}

#description-text { 
  margin: 0 20px 0 5px;
  resize: 20px;
  width: 110px;
}

#date-select { 
  border: none;
  background-color: rgba(255, 255, 255, 0);
  margin: 0 20px 0 5px;
}

#date-select:focus-visible { 
  outline: none;
  border-bottom: 1px solid black;
}

#priority-select { 
  border: none;
  background-color: rgba(255, 255, 255, 0);
  margin: 0 20px 0 5px;
  font: inherit;
}

#priority-select:focus-visible { 
  outline: none;
  border-bottom: 1px solid black;
}

input[type="checkbox" i] {
  margin: 5px;

}

.todo-delete { 
  background-color: rgba(255, 255, 255, 0);
  font-family: inherit;
  font-size: small;
  border: 1px solid black;
  border-radius: 2px;
}

.todo-delete:hover { 
  background-color: rgba(236, 65, 65, 0.89);
}`, "",{"version":3,"sources":["webpack://./src/styles.css"],"names":[],"mappings":"AAAA;EACE,qBAAqB;EACrB,oCAAoC;EACpC,SAAS;EACT,aAAa;EACb,aAAa;EACb,YAAY;AACd;;AAEA;EACE,aAAa;EACb,mBAAmB;EACnB,SAAS;EACT,mBAAmB;AACrB;;AAEA;EACE,iBAAiB;EACjB,iBAAiB;AACnB;;AAEA;EACE,YAAY;AACd;;AAEA;EACE,oCAAoC;EACpC,aAAa;EACb,sBAAsB;EACtB,OAAO;EACP,aAAa;AACf;;AAEA;EACE,aAAa;EACb,sBAAsB;EACtB,QAAQ;AACV;;AAEA;EACE,YAAY;AACd;;AAEA;EACE,4CAA4C;EAC5C,wBAAwB;AAC1B;;AAEA;EACE,YAAY;EACZ,eAAe;EACf,0BAA0B;EAC1B,wCAAwC;EACxC,mBAAmB;EACnB,aAAa;EACb,uBAAuB;EACvB,mBAAmB;AACrB;;AAEA;EACE,4CAA4C;AAC9C;;AAEA;EACE,YAAY;AACd;;AAEA;EACE,OAAO;EACP,aAAa;AACf;;AAEA;EACE,eAAe;EACf,iBAAiB;EACjB,mBAAmB;AACrB;;AAEA;EACE,aAAa;EACb,QAAQ;AACV;;;AAGA;EACE,4CAA4C;AAC9C;;AAEA;EACE,aAAa;EACb,2BAA2B;EAC3B,UAAU;EACV,SAAS;AACX;AACA;EACE,aAAa;EACb,gBAAgB;EAChB,wCAAwC;EACxC,YAAY;EACZ,YAAY;EACZ,WAAW;EACX,aAAa;AACf;;;AAGA;EACE,aAAa;EACb,8BAA8B;AAChC;;AAEA;EACE,aAAa;EACb,8BAA8B;EAC9B,4BAA4B;AAC9B;;AAEA;EACE,aAAa;EACb,mBAAmB;AACrB;;AAEA;EACE,4CAA4C;AAC9C;;AAEA;EACE,aAAa;EACb,2BAA2B;EAC3B,mBAAmB;EACnB,iBAAiB;EACjB,gBAAgB;EAChB,YAAY;AACd;;AAEA;EACE,oBAAoB;EACpB,YAAY;EACZ,YAAY;AACd;;AAEA;EACE,YAAY;EACZ,wCAAwC;EACxC,oBAAoB;AACtB;;AAEA;EACE,aAAa;EACb,8BAA8B;AAChC;;AAEA;EACE,YAAY;EACZ,wCAAwC;EACxC,oBAAoB;EACpB,aAAa;AACf;;AAEA;EACE,aAAa;EACb,8BAA8B;AAChC;;AAEA;EACE,WAAW;;AAEb;;AAEA;EACE,wCAAwC;EACxC,oBAAoB;EACpB,gBAAgB;EAChB,uBAAuB;EACvB,kBAAkB;AACpB;;AAEA;EACE,yCAAyC;AAC3C","sourcesContent":["body {\n  font-family: Consolas;\n  background-color: rgb(249, 249, 249);\n  margin: 0;\n  display: flex;\n  height: 100vh;\n  width: 100vw;\n}\n\n#sidebar-title-container { \n  display: flex;\n  align-items: center;\n  gap: 10px;\n  margin-bottom: 10px;\n}\n\n#sidebar-title { \n  font-size: larger;\n  font-weight: bold;\n}\n\n#todo-logo { \n  height: 30px;\n}\n\n#sidebar { \n  background-color: rgb(241, 245, 249);\n  display: flex;\n  flex-direction: column;\n  flex: 1;\n  padding: 10px;\n}\n\n#sidebar-projects {\n  display: flex;\n  flex-dire